nav{
    background: rgba(0,0,0,0.3);
    width : max-content;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left : 50%;
    transform: translateX(-50%);
    bottom : 1rem;
    display: flex;
    gap: 1.2rem;
    border-radius : 3rem;
    backdrop-filter: blur(15px); 
}

nav a {
    background: transparent;
    padding: 0.9rem;
    border-radius : 50%;
    display: flex;
    color: var(--color-lite);
    font-size: 1.5rem;
}

nav a:hover{
    background: rgba(0,0,0,0.2);
}

nav a.active{
    background: var(--color-primary);
    color : var(--color-bg);
}